.Toolbar {
    height: 56px;
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 20px;
    box-sizing: border-box;
    background-color: rgba(31, 36, 36, 0.9);
    z-index: 90;
}

.Toolbar nav{
    height: 100%;
}

@media (max-width: 499px){
    .DesktopOnly{
        display: none;
    }
}