.Training{
    width: 100%;
    margin: auto;
    height: 250px;
    text-align: center;
    font-weight: bold;
    font-size: 1.2rem;
}

@media (min-width: 500px) and (min-heigth: 400px){
    .Training{
        width: 350px;
        height: 300px;
    }
}

@media (min-width: 500px) and (min-heigth: 401px){
    .Training{
        width: 450px;
        height: 400px;
    }
}

@media (min-width: 1000px) and (min-heigth: 700px){
    .Training{
        width: 700px;
        height: 600px;
    }
}